.transaction-history {
    padding: 20px;

    &-container {
        display: flex;
        flex-direction: column;
        .nav-tabs {
            border-bottom: 0;
            margin-bottom: 10px;
            margin-left: 10px;
            .nav-link {
                font-family: Poppins-Medium;
                cursor: pointer;
                color: white;
                border: none;
                background-color: rgba(0, 0, 0, 0);
                margin: 0;
                padding: 10px 20px;
                &:hover {
                    background-color: rgba(0, 0, 0, 0);
                }
                &.active {
                    box-shadow: none !important;
                    background-color: rgba(0, 0, 0, 0);
                    border: none;
                    background-image: linear-gradient(90deg, #33CDE1 0%, #7031EF 50%, #F342F8 100%);
                    background-position: 0 90%, 100% 100%;
                    background-size: 100% 2px;
                    background-repeat: no-repeat;
                    transition: none;
                }
            }
        }
    }
    .custom-table {
        &-bottom {
            flex-wrap: wrap;
            .pagination {
                margin-top: 1rem;
            }
        }
        
    }
    .custom-table-bottom {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;

        .dropdown button {
            background-color: transparent;
            border: 1px solid #515189;
            color: white;
        }

        .btn-primary:not(:disabled):not(.disabled):active, .btn-primary:not(:disabled):not(.disabled).active, .show > .btn-primary.dropdown-toggle {
            background-color: transparent;
            border: 1px solid #515189;
        }

        &-left {
            display: flex;
            align-items: center;

            &-status {
                margin-left: 24px;
                color: white;
            }
        }

        .pagination {
            margin: 0;
            .page-item {
                a {
                    background-color: #27235A;
                    color: white;
                    margin: 0 8px;
                    border: none;
                }
                &.disabled span {
                    background-color: #27235A;
                    color: white;
                    border: none;
                }
            }

            .page-item.active .page-link {
                background: #F843EA;
            }
        }
    }
    
}
@media (min-width:920px) {
	.mobile-data-list {
        display: none;
    }
    .custom-table {
        display: block;
    }
}
@media (max-width:920px) {
	.mobile-data-list {
        display: block;
    }
    .custom-table {
        display: none;
    }
}