$scale-var: 1600 / 2560;

.fill-input {
	width: 100%;
	input {
		border-radius: 0 !important;
	}
	input[type=number]::-webkit-inner-spin-button, 
	input[type=number]::-webkit-outer-spin-button { 
	-webkit-appearance: none; 
	margin: 0; 
	}
}
.purple-bg-input-child {
	background: linear-gradient(90deg, #3C3C97 0%, #363075 100%);
	input {
		background-color: rgba(0, 0, 0, 0) !important;
		

	}
	.md-form label {
		position: absolute;
		top: 0;
		left: 0;
		font-size: 1rem;
		color: white;
		cursor: text;
		transition: transform .2s ease-out, color .2s ease-out;
		transform: translateY(12px);
		transform-origin: 0% 100%;
	}
}
.moon-bet-input {
	.md-form.md-bg .form-control {
		padding-bottom: 5px !important;
		padding-left: 24px !important;
		color: white;
	}
}
.bet-type {
	align-items: center;
	padding-top: 18px * $scale-var !important;
	padding-left: 5% !important;
	padding-right: 5% !important;
	padding-bottom: 18px * $scale-var !important;
	.title {
		color: #5C52C6;
		font-family: Nurom;
		font-weight: bold;
		font-size: 20px * $scale-var;
		margin-bottom: 22px * $scale-var;
		text-align: center;
	}
	.manual-auto-toggle {
		width: 100%;
		display: flex;
		justify-content: center;
		border: none;
		.select-button {
			width: 50% !important;
			text-align: center;
		}
	}

	.nav-tabs {
		.nav-item {
			width: 50%;
			height: 47px * $scale-var;
			border: none;
			.nav-link {
				height: 100%;
				width: 100%;
				background-color: #322C76 !important;
				border: none;
				border-radius: 0;
				font-family: Nurom;
				font-size: 12px;
				color: white !important;
				display: flex;
				justify-content: center;
				align-items: center;
			}
			.active {
				background-color: #F001F4 !important;
			}
		}
		
	}
	.tab-content {
		width: 100%;
		.tab-pane {
			margin-top: 24px * $scale-var;
			width: 100%;
		}
	}
	.multi {
		display: flex;
	}
	.bet-detail {
		width: 100%;
		position: relative;
		
		.title {
			color: white;
			font-family: Poppins-Regular;
			font-size: 12px * $scale-var;
			height: 18px;
			text-align: left;
			font-weight: normal;
			position: absolute;
			left: 22px * $scale-var;
			top: 5px;
		}
		.main-description {
			color: white;
			font-family: Nurom;
			font-weight: bold;
			font-size: 21px * $scale-var;
			position: absolute;
			left: 22px * $scale-var;
			top: 28px * $scale-var;
		}
		.sub-detail-content {
			max-width: 50%;
			flex-wrap: nowrap;
			position: absolute;
			right: 10px;
			transform: translateY(-50%);
			top: 50%;
			display: flex;
			justify-content: right;
			.btn-group {
				box-shadow: none !important;
				.btn-check {
					
					&+label {		
						border-radius: 5px;
						background-color: rgba(0, 0, 0, 0) !important;
						padding: 5px 10px;
						font-size: 12px;
						font-family: Poppins-Regular;
					}
					&:checked {
						&+label {		
							background-color: #F001F4  !important;
						}
					}
				}
			}
			.detail {
				padding-left: 5px;
				padding-right: 5px;
				font-family: Poppins-Regular;
				font-size: 17px * $scale-var;
				color: white;
				svg {
					color: #F843EA !important;
				}
				&.form-check {
					position: relative;
					display: flex;
					justify-self: center;
					align-items: center;
					width: 60px;
					padding: 0;
					.form-check-input {
						position: absolute !important;
						transform: translate(-50%, -50%);
						left: 50%;
						top: 50%;
						border-radius: 5px;
						background-color: rgba(0, 0, 0, 0);
						border: none;
						margin: 0;
						float: none;
						width: 100%;
						height: 100%;
						&:checked {
							position: absolute;
			
							background-image: none;
							background-color: hotpink;
							
							border: none;
							&+label {
								color: black !important;
							}
						}
					}
					.form-check-label {
						position: absolute !important;

						text-align: center;
						transform: translate(-50%, -50%);
						left: 50%;
						top: 50%;
						font-family: Poppins-Regular;
						font-size: 12px;
						&:focus {
							position: absolute;
							
							
						}
					}
				}
			}
			.amount-change-btn {
				cursor: pointer;
				&:hover {
					color: #F843EA;
				}
				&:focus {
					background-size: 200% 200%;
					background-color: rgba(0, 0, 0, 0.2);

				}
			}
		}
	}
	.bet-amount {
		
		background-color: #3C3C97;
		width: 100%;
		height: 66px * $scale-var;
	}
	.auto-cashout {
		margin-top: 15px * $scale-var;
		background-color: #3C3C97;
		width: 100%;
		color: white;
		height: 66px * $scale-var;
	}
	.play-button {
		margin: 0;
		margin-top: 15px * $scale-var;
		background-color: #F843EA !important;
		width: 100%;
		color: white;
		height: 66px * $scale-var;
		text-align: center;
		position: relative;
		white-space: nowrap;
		border: none;
		border-radius: 0;
		padding: 0;
		
		span {
			text-align: center;
			font-family: Nurom;
			font-size: 22px * $scale-var;
			font-weight: bold;
			transform: translate(-50%, -50%);
			top: 50%;
			left: 50%;
			position: absolute;
			
		}
		&[disabled] {
			background-color: gray;
		}
	}
	.bet-info-detail {
		margin-top: 15px * $scale-var;
		background-color: #3C3C97;
		width: 100%;
		color: white;
		height: 66px * $scale-var;
		position: relative;
	}
	.total-bet {
		margin-top: 15px * $scale-var;
		background-color: #3C3C97;
		width: 100%;
		color: white;
		height: 66px * $scale-var;
	}
	.my-recent-wins {
		margin-top: 15px * $scale-var;

		max-height: 200px;
		.title {
			color: white;
			
			font-size: 20px * $scale-var;
			font-weight: bold;
			text-align: center;
			background: linear-gradient(90deg, #3C3C97 0%, #363075 100%);
			height: 30px;
			position: relative;
			margin: 0;
			span {
				font-family: Nurom;
				font-size: 12px;
				position: absolute;
				transform: translate(-50%, -50%);
				top: 50%;
				left: 50%;
				white-space: nowrap;
			}
		}
		.table {
			width: 100%;
			margin-bottom: 0 !important;
			th {
				font-family: Poppins-SemiBold;
				font-weight: semi-bold;
				font-size: 17px * $scale-var;
				height: 50px * $scale-var;
				padding-left: 15px;
				border: none;
			}
			tr {
				border: none;
				color: white;
				height: 42px * $scale-var;
				
			}
			td {
				padding: 6px;
				padding-left: 15px;
				border: none;
				font-family: Poppins-Regular;
				font-size: 17px * $scale-var;
			}
			tr:nth-child(even) {
				background-color: #3C3C97;
			}
			td:nth-child(even) {
				color: #78FF15;
			}
		}
	}
}

